function * watchUserAuthentication () {
  yield takeEvery(SIGNIN_REQUEST, onHandleSignInRequest);
}

function * watchUserUnAuthentication () {
  yield takeEvery(SIGNOUT_REQUEST, onHandleSignOutRequest);
}

function * watchGoogleAuthentication() {
   yield takeLatest(GOOGLE_AUTH_CALLBACK_REQUEST, onHandleGoogleAuthCallback);
}

function* watchUpdateUserOrgId() {
  yield takeLatest(UPDATE_USER_ORG_ID, onHandleUpdateUserOrgId);
}

export default function * sagas () {
  yield all([
    watchUserAuthentication(), 
    watchUserUnAuthentication(), 
    watchGoogleAuthentication(),
    watchUpdateUserOrgId()
  ]);
}
