import { createStore, applyMiddleware, compose } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import createSagaMiddleware from 'redux-saga';
import { createBrowserHistory } from 'history';
import reduxThunk from 'redux-thunk';
import { createLogger } from 'redux-logger';
import { routerMiddleware } from 'react-router-redux';
import createReduxPromiseListener from 'redux-promise-listener';

import env from './lib/env';
import rootReducer from './modules/rootReducer';
import sagas, { logActions } from './modules/rootSagas';

const sagaMiddleware = createSagaMiddleware();
const reduxPromiseListener = createReduxPromiseListener();

const basename = '/';

const composeEnhancers =
  (typeof window !== 'undefined' && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__) ||
    env.REACT_APP_ENVIRONMENT === 'dev' ? composeWithDevTools({ trace: true, traceLimit: 25 }) : compose;

export const history = createBrowserHistory({ basename });
const middleware = [
  sagaMiddleware,
  reduxThunk,
  reduxPromiseListener.middleware,
  routerMiddleware(history)
];

if (process.env.NODE_ENV !== 'production') {
  middleware.push(createLogger());
}

const store = createStore(
  rootReducer(history),
  /* preloadedState, */ composeEnhancers(
    applyMiddleware(...middleware)
  ));

export const promiseListener = reduxPromiseListener; // <----------  IMPORTANT 

sagaMiddleware.run(logActions);
sagaMiddleware.run(sagas);

export default store;
export const dispatch = store.dispatch;
