// Delete Tutor Group
export const handleDeleteTutorGroupRequest = (payload) => ({
  type: DELETE_TUTOR_GROUP_REQUEST,
  payload
});

export const handleDeleteTutorGroupSuccess = (payload) => ({
  type: DELETE_TUTOR_GROUP_SUCCESS,
  payload
});

export const handleDeleteTutorGroupFailure = (payload) => ({
  type: DELETE_TUTOR_GROUP_FAILURE,
  payload
});

export function * onHandleDeleteTutorGroupRequest ({ payload }) {
  console.log('#onHandleDeleteTutorGroupRequest, payload', payload);

  try {
    console.log('#onHandleDeleteTutorGroupRequest, try block');

    const response = yield call(
      request.del,
      `/classes/${payload.classId}/tutor-groups/${payload.tutorGroupId}`
    );
    console.log('#onHandleDeleteTutorGroupRequest, response: ', response);

    return yield all([
      put(
        handleDeleteTutorGroupSuccess({ tutorGroupId: payload.tutorGroupId })
      ),
      put(push(`/teacher/classes/${payload.classId}`))
    ]);
  } catch (err) {
    console.error('Error, #onHandleDeleteClassRequest, err: ', err);

    const { status, message } = err.response.data;
    let errorMessage = '';
    if (message === 'Network Error') {
      errorMessage =
        'Network Error? Check your connection and please try again later...';
    }

    if (status === 422 || status === 400) {
      errorMessage = message;
    }

    if (err.response.status === 500) {
      errorMessage = 'Its not you, its us....... Please try again later.';
    }

    return yield put({
      type: DELETE_TUTOR_GROUP_FAILURE,
      payload: errorMessage
    });
  }
}

export const handleChangeTutorGroupStatusRequest = (payload) => ({
  type: CHANGE_TUTOR_GROUP_STATUS_REQUEST,
  payload
});

export const handleChangeTutorGroupStatusSuccess = (payload) => ({
  type: CHANGE_TUTOR_GROUP_STATUS_SUCCESS,
  payload
});

export const handleChangeTutorGroupStatusFailure = (payload) => ({
  type: CREATE_TUTOR_GROUP_FAILURE,
  payload
});

export function * onHandleChangeTutorGroupStatusRequest ({ payload }) {
  const { sessionId, status, classId, tutorGroupId } = payload

  const statusParam = (status === STOPPED) && 'stop'

  try {
    console.log('#onHandleChangeTutorGroupStatusRequest, try block');

    const response = yield call(
      request.post,
      `/cohort-sessions/${sessionId}/${statusParam}`
    );

    return yield all([
      put(
        handleChangeTutorGroupStatusSuccess()
      ),
      // refetch tutor-group
      put(
        handleTutorGroupRequest({ classId, tutorGroupId })
      )
    ]);
    
  } catch (err) {
    console.error('Error, #onHandleChangeTutorGroupStatusRequest, err: ', err);

    const { status, message } = err.response.data;
    let errorMessage = '';
    if (message === 'Network Error') {
      errorMessage =
        'Network Error? Check your connection and please try again later...';
    }

    if (status === 422 || status === 400) {
      errorMessage = message;
    }

    if (err.response.status === 500) {
      errorMessage = 'Its not you, its us....... Please try again later.';
    }

    return yield put({
      type: CHANGE_TUTOR_GROUP_STATUS_FAILURE,
      payload: errorMessage
    });
  }
}